const app = getApp()
export default {
    data() {
        return {
            show_popup: 0,
            withdraw_amount: '',
            user: {}
        };
    },
    async onLoad(){
        const res = await app.request(this.$Url + '/clmsj/user/getUser')
        this.user = res.data.data.user
    },
    methods: {
        hidePopup() {
            this.show_popup = 0;
        },
        showPopup() {
            this.show_popup = 1;
        },
        navTo(url){
            uni.navigateTo({
                 url
            });
        },
        withdrawConfirm() {
            if(!this.withdraw_amount){
                this.$api.msg('请输入正确的金额')
                return
            }

            this.show_popup = 0
            
            if(this.withdrawing){
                return
            }
            this.withdrawing = 1


        }
    }
};
